.image {
  display: block;

  margin-left: auto;
  margin-right: auto;
}

.title {
  composes: text text_type_main-large from global;
  text-align: center;
}

.name {
  display: block;

  composes: text text_type_main-medium from global;
  text-align: center;
}

.flexWrapper {
  display: flex;
  justify-content: center;
}

.text {
  display: block;

  composes: text from global;
  composes: text_color_inactive from global;
}

.amount {
  text-align: center;

  composes: text_type_digits-default from global;
}
